@font-face {
    font-family:'ProximaNova';
    src: url('../fonts/Proxima Nova Regular.eot');
  src: url('../fonts/Proxima Nova Regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Proxima Nova Regular.woff') format('woff'),
    url('../fonts/Proxima Nova Regular.ttf') format('truetype'),
    url('../fonts/Proxima Nova Regular.svg#a Regular') format('svg');
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
}
@font-face {
    font-family:'ProximaNova';
    src: url('../fonts/Proxima Nova Bold.eot');
	  src: url('../fonts/Proxima Nova Bold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Proxima Nova Bold.woff') format('woff'),
		url('../fonts/Proxima Nova Bold.ttf') format('truetype'),
		url('../fonts/Proxima Nova Bold.svg#a Bold') format('svg');
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    unicode-range: U+0020-0491;
}
